<template>
  <div class="editor-header">
    <div class="left">
      <Dropdown :trigger="['click']">
        <div class="menu-item"><IconEdit /> <span class="text"></span></div>
        <template #overlay>
          <Menu>
            <MenuItem @click="undo()"></MenuItem>
            <MenuItem @click="redo()"></MenuItem>
            <MenuItem @click="createSlide()"></MenuItem>
            <MenuItem @click="deleteSlide()"></MenuItem>
            <MenuItem @click="toggleGridLines()">{{ showGridLines ? '' : '' }}</MenuItem>
            <MenuItem @click="resetSlides()"></MenuItem>
          </Menu>
        </template>
      </Dropdown>
      <Dropdown :trigger="['click']">
        <div class="menu-item"><IconPpt /> <span class="text"></span></div>
        <template #overlay>
          <Menu>
            <MenuItem @click="enterScreeningFromStart()"></MenuItem>
            <MenuItem @click="enterScreening()"></MenuItem>
          </Menu>
        </template>
      </Dropdown>
      <Dropdown :trigger="['click']">
        <div class="menu-item"><IconHelpcenter /> <span class="text"></span></div>
        <template #overlay>
          <Menu>
            <MenuItem @click="openDoc()"></MenuItem>
            <MenuItem @click="hotkeyDrawerVisible = true"></MenuItem>
          </Menu>
        </template>
      </Dropdown>
    </div>

    <div class="right">
      <Tooltip :mouseLeaveDelay="0" title="">
        <div class="menu-item" @click="enterScreening()">
          <IconPpt size="18" fill="#666" style="margin-top: 2px;" />
        </div>
      </Tooltip>
      <Tooltip :mouseLeaveDelay="0" title="Github ">
        <a href="https://github.com/pipipi-pikachu/PPTist" target="_blank">
          <div class="menu-item"><IconGithub size="18" fill="#666" /></div>
        </a>
      </Tooltip>
    </div>

    <Drawer
      width="320"
      placement="right"
      :visible="hotkeyDrawerVisible"
      @close="hotkeyDrawerVisible = false"
    >
      <HotkeyDoc />
    </Drawer>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, ref } from 'vue'
import { MutationTypes, useStore } from '@/store'
import { createRandomCode } from '@/utils/common'
import useScreening from '@/hooks/useScreening'
import useSlideHandler from '@/hooks/useSlideHandler'
import useHistorySnapshot from '@/hooks/useHistorySnapshot'

import HotkeyDoc from './HotkeyDoc.vue'
import { message } from 'ant-design-vue'

export default defineComponent({
  name: 'editor-header',
  components: {
    HotkeyDoc,
  },
  setup() {
    const store = useStore()

    const { enterScreening, enterScreeningFromStart } = useScreening()
    const { createSlide, deleteSlide } = useSlideHandler()
    const { redo, undo } = useHistorySnapshot()

    const showGridLines = computed(() => store.state.showGridLines)
    const toggleGridLines = () => {
      store.commit(MutationTypes.SET_GRID_LINES_STATE, !showGridLines.value)
    }

    const resetSlides = () => {
      store.commit(MutationTypes.SET_ACTIVE_ELEMENT_ID_LIST, [])
      store.commit(MutationTypes.SET_SLIDES, [{
        id: createRandomCode(),
        elements: [],
      }])
    }

    const openDoc = () => {
      message.warning('...')
    }

    const hotkeyDrawerVisible = ref(false)

    return {
      enterScreening,
      enterScreeningFromStart,
      createSlide,
      deleteSlide,
      redo,
      undo,
      toggleGridLines,
      showGridLines,
      resetSlides,
      openDoc,
      hotkeyDrawerVisible,
    }
  },
})
</script>

<style lang="scss" scoped>
.editor-header {
  background-color: #fff;
  user-select: none;
  border-bottom: 1px solid $borderColor;
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
}
.left, .right {
  display: flex;
  justify-content: center;
  align-items: center;
}
.menu-item {
  height: 70%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 13px;
  margin: 0 10px;
  cursor: pointer;

  .text {
    margin-left: 4px;
  }
}
</style>